@use './src/assets/colors.scss';

*,
*::before,
*::after {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

html {
	font-size: 62.5%;
}

body {
	min-height: 100vh;
	background: colors.$bg-gradient;
	overflow-x: hidden;
}

body,
button {
	font-family: 'Barlow Semi Condensed', sans-serif;
	font-size: 1.6rem;
}
